body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Styles for the play component if needed. */
/* Styles for the header component if needed. */
h2 {
  margin-bottom: 0;
}

.navbar {
  background-color: black;
  color: white;
}

.navbar-brand {
  color: white !important;
  font-size: 30px;
}

/* Styles for the footer component if needed. */
footer {
  height: 100px;
  position: relative;
  color: #fff;
  overflow: auto;
  /* background: radial-gradient(#2A2699, #6CADDC); */
  background: -webkit-radial-gradient( top, #6CADDC, #2A2699 );
  background: radial-gradient( top, #6CADDC, #2A2699 );
}

.pageFooter {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 30px;
  line-height: 30px;
  margin-bottom: 0px;
  background-color: #2A2699;
}


/* Styles for the instruction component if needed. */
.instructArea {
  color: blue;
  background: -webkit-radial-gradient(bottom, #6CADDC, #2A2699);
  background: radial-gradient(bottom, #6CADDC, #2A2699);
  /* background-color: black; */
  margin-left: 0;
  margin-right: 0;
  width: 100%;
  height: 40vh;
  /* height: 50%; */
  display: flex;
  align-items: center;
}

.instructText {
  color: White;
  margin: auto;
}
.container {
  display: flex;
  min-height: 80%;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 50px;
  padding-bottom: 50px;
  width: 75%;
  margin: 0 auto;
}

.card {
  display: flex;
  flex-wrap: wrap;
  background: #fff;
  border-radius: 2px;
  height: 200px;
  margin: 1rem;
  position: relative;
  width: 200px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;

}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}

.card > .img-container > img {
  margin-left: auto;
  margin-right: auto;
  margin-top: 10%;
  margin-bottom: 10%;
  width: 80%

}

